/*
 *  Define the magnific popup styles
 */

.popup {
  max-width: 560px;
  margin: 0 auto;
  padding: 25px 25px 25px;
  background-color: $white;

  @include min($tablet) {
    padding: 50px 75px 25px;
  }

  &.popup--email {
    text-align: center;

    .email {
      width: 100%;
    }
  }

  .wpcf7-form-control-wrap,
  .form-row {
    margin-bottom: 2em;
  }

  .wpcf7-form-control-wrap {
    display: block;
  }

  div.wpcf7-mail-sent-ok {
    border: none;
    position: absolute;
  }

  .button {
    margin-top: 0.5em;
    width: 75%;
    -webkit-appearance: none;
  }
}

.popup__title {
  margin-bottom: 0.5em;
  text-align: center;
}

.popup__body {
  text-align: center;
  font-weight: 300;
}

.form-label {
  display: block;
}

.form-input {
  width: 100%;
}

.wpcf7-form {
  .wpcf7-form-control {
    width: 100%;
  }
}

.button--form {
  margin: 0 auto;
  width: 50%;
}

// Classes for modal animation
.mfp-fade.mfp-bg {
  opacity: 0;
  transition: opacity 700ms ease;
}

.mfp-fade {

  &.mfp-ready {
    .popup {
      transform: translateY(0);
      opacity: 1;
    }

    .mfp-figure {
      transform: translateY(0);
      opacity: 1;
    }
  }

  &.mfp-removing {
    .popup {
      transform: translateY(-50px);
      opacity: 0;
    }

    .mfp-figure {
      transform: translateY(-50px);
      opacity: 0;
    }
  }

  .popup {
    transform: translateY(50px);
    opacity: 0;
    transition: all 600ms ease;
  }
}

// Contact Form 7 Override
.wpcf7-form {
  p {
    margin-bottom: 0;
  }
}

/* overlay animate in */
.mfp-fade.mfp-bg.mfp-ready {
  opacity: $mfp-overlay-opacity;
}

/* overlay animate out */
.mfp-fade.mfp-bg.mfp-removing {
  opacity: 0;
  transition-delay: 300ms;
}

.mfp-figure {
  transform: translateY(20px);
  opacity: 0;
  transition: all 600ms ease;
}
